@Component({
  selector: 'app-new-user-registration',
  templateUrl: './new-user-registration.component.html',
  styleUrls: ['./new-user-registration.component.scss']
})
export class NewUserRegistrationComponent implements OnInit {

  userTypeList:Array<any>=[];
  bloodGroupData:Array<any>=[];
  userType:any;
  addFamilyList:Array<any>=[];

  constructor(public dialogRef: MatDialogRef<NewUserRegistrationComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,private apartmentService : ApartmentService,
    private lookupService: LookupService,private sharedService : SharedService, private userService : UserService, private cookieService: CookieService,private todoService:TodoListService) {
  }

  ngOnInit(): void {
    this.userService.getAllRoleTypes().subscribe(
      (resp:any) =>{
        this.userTypeList = resp;
      }
    );
    
    let groupParams = {
      LookupTypeId: 24
    }

    this.lookupService.getLookupValueByLookupTypeId(groupParams).subscribe((res:any) => {
      this.bloodGroupData = res;
    });

    let staffParams = {
      LookupTypeId: 25
    }
  }

  addFamily(){
    this.addFamilyList.push({});
  }

  changeUserType(evt){
    // this.isAdminRegister = false;
    // if(this.userType == 1){
    //   this.isAdminRegister = true;
    // }else if(this.userType == 3){
    //   this.isAdminRegister = false;
    // }
}

addFamilyDetails(){
  this.addFamilyList.filter(key =>{
    let details = {
      "firstName": key.firstName,
      "middleName": key.middleName,
      "lastName": key.lastName,
      "relationship": key.relationship,
      "relatedUserId": parseInt(this.cookieService.get('userId')),
      "emailId": key.emailId,
      "genderId": parseInt(key.genderType),
      "dob": key.dob,
      "bloodGroup": key.bloodGroup,
      "phoneNumber": key.phoneNumber.toString(),
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": 0,
      "updatedOn": "2020-03-28T10:44:31.345Z"
    }
    let params = {
      userFamilyMember: details
    }
    this.userService.addUserFamilyMember(params).subscribe((res:any) => {
      if(res.message){
        this.sharedService.setAlertMessage("Family Member added successfully");
      }
      else {
      }
    })
  })
}

updateUser(){
        //edit user
        var userDetails = {
          "userId": this.data.userId,
          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "firstName": this.data.firstName,
          "middleName": this.data.middleName,
          "lastName": this.data.lastName,
          "emailId": this.data.emailId,
          "password": this.data.password,
          "salt": this.data.salt,
          "address1": this.data.address1,
          "address2": this.data.address2,
          "city": this.data.city,
          "state": this.data.state,
          "country": this.data.country,
          "zipCode": this.data.zipCode,
          "genderId": parseInt(this.data.genderType),
          "dob": this.data.dob,
          "joinedOn": this.data.joinedOn,
          "bloodGroup": this.data.bloodGroup,
          "phoneNumber": this.data.phoneNumber.toString(),
          "emergencyContactNumber": this.data.emergencyContactNumber,
          "emergencyContactPerson": this.data.emergencyContactPerson,
          "emergencyContactNumberSecondary": this.data.emergencyContactNumberSecondary,
          "emergencyContactPersonSecondary": this.data.emergencyContactPersonSecondary,
          "accessCardNumber": this.data.accessCardNumber,
          "notes":  this.data.notes,
          "profilePictureId": null,
          "isLiving": this.data.isLiving,
          "isActive": true,
          "isApproved": this.data.isApproved,
          "resetPassword": this.data.resetPassword,
          "insertedBy": this.data.insertedBy,
          "updatedBy": parseInt(this.cookieService.get('userId')),
          "isDocSubmitted": this.data.isDocSubmitted,
          "readyForApproval": this.data.readyForApproval
        }
        let params = {
          user: userDetails
        }
        //edit user 
        this.userService.updateUser(params).subscribe((res:any) => {
              if(res.message) {
                var userId = res.message;
                var apartmentBlockUnitUser = {
                  // "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
                  "userId": userId, 
                  "isPrimaryContact":this.data.isPrimaryContact,
                  "userTypeId": parseInt(this.userType),
                  "isActive": this.data.isActive,
                  "insertedBy": parseInt(this.cookieService.get('userId')),
                  "insertedOn": this.data.insertedOn,
                  "updatedBy": this.data.updatedBy,
                  "updatedOn": this.data.updatedOn
                }
  
                let apartmentParams = {
                  apartmentBlockUnitUser: apartmentBlockUnitUser
                }
  
                this.apartmentService.addApartmentBlockUnitUser(apartmentParams).subscribe((res:any) => {
  
                  if(res.message) {
                    var userRole = {
                      "userId": userId,
                      "roleId": parseInt(this.userType),
                      "isActive": this.data.isActive,
                      "insertedBy": parseInt(this.cookieService.get('userId')),
                      "insertedOn": this.data.insertedOn,
                      "updatedBy": this.data.updatedBy,
                      "updatedOn": this.data.updatedOn,
                      "apartmentBlockUnitUserId":res.message
                    }
                    let params = {
                      userRole: userRole
                    }
    
                    this.userService.addUserRole(params).subscribe((res:any) => {
                      this.sharedService.setAlertMessage("User updated successfully");
                      // this.router.navigate(['ams/unit&users/unapproved']);
                    },
                    error => {
                      console.log(error);
                    }
                    );
  
                  }
                  else {
                      // this.isError = true;
                      // this.errorMessage = res.errorMessage;
                  }
                },
                error => {
                  console.log(error);
                }
                );
              }
        },
        error => {
          
        }
        );
}

}
